html {
  font-size: 100%;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1440px;
  margin: 0 auto;
}

picture {
  width: 100%;
  height: 100%;
}

.hero {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 610px;
}
.hero .logo {
  margin-top: 74px;
  margin-bottom: 136px;
}
.hero__text {
  width: 445px;
}
.hero__text h1 {
  font-family: "Josefin Sans", sans-serif;
  line-height: 110%;
  font-size: 64px;
  letter-spacing: 17px;
  color: #423a34;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 32px;
}
.hero__text .heading {
  color: #ce9898;
  font-weight: 300;
  line-height: 100%;
}
.hero__text p {
  font-family: "Josefin Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  color: #ce9898;
  margin-bottom: 40px;
}
.hero__text form {
  position: relative;
}
.hero__text button {
  position: absolute;
  top: 0;
  right: 0;
  width: 100px;
  height: 56px;
  background: linear-gradient(135deg, #f8bfbf 0%, #ee8b8b 100%);
  box-shadow: 0px 15px 20px rgba(198, 110, 110, 0.247569);
  border-radius: 28px;
  border: none;
  outline: none;
  cursor: pointer;
}
.hero__text button:focus {
  border: none;
}
.hero__text input {
  position: absolute;
  width: 100%;
  border: 1px solid #ce9898;
  border-radius: 28px;
  padding: 15px 0 13px 32px;
  font-size: 16px;
  line-height: 28px;
  color: #ce9898;
  font-family: "Josefin Sans", sans-serif;
}
.hero__img {
  grid-column: 2/3;
  grid-row: 1/3;
  width: 100%;
}
.hero__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 600px) {
  .hero {
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .hero .logo {
    grid-column: 1/1;
    grid-row: 1/2;
    padding: 31px 0 31px 31px;
    margin: 0;
  }
  .hero__img {
    grid-column: 1/-1;
    grid-row: 2/3;
  }
}